/**
 * 注册监听
 * @author like
 */
import { cache } from '../store.pool'
import { vmKey, dataKey } from '../constants'

const vm = cache.get(vmKey)
let subject = null
export class Subject {

    /**
     * for route easy post
     * 用来处理路由的传输数据
     */
    constructor() {
        this.initSingle()
    }
    initSingle() {

        /**
         * subscribe router
         * 注册路由
         */
        subject.prototype.subscribe = function subscribe(vue) {
            if (vue.subject) {
                subject = vue.subject
            } else {
                subject = new Subject()
            }
            const routeKey = vue.$route.name
            const changeData = {
                routeKey
            }
            cache.set(dataKey, changeData)
            vue.prototype.subject = subject // 双向绑定
        }

        /**
         * save value
         * 储存value
         */
        subject.prototype.next = function next(key, value) {
            cache.set(key, value)
        }

        /**
         * clean cache
         * 清理缓存
         */
        subject.prototype.clear = function clear() {
            cache.clear()
            cache.set(vmKey, vm)
        }

        /**
         * delete one value
         * 删除某个值
         */
        subject.prototype.remove = function remove(key) {
            cache.delete(key)
        }

        /**
         * find one value
         * 获取某个值
         */
        subject.prototype.getValue = function getValue(key) {
            cache.get(key)
        }

        /**
         * to test Api
         * 供给测试API
         */
        subject.prototype.getJson = function getJson() {
            return cache.toJSON()
        }
    }
}

export default subject